body {
  color: var(--primary-text-color);
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.333;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
}

* {
  box-sizing: border-box;
}

.container {
  width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 32px;
  padding-right: 32px;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.home-page, .library-page {
  padding-top: 60px;
  padding-bottom: 60px;
}

.footer {
  background-color: #f7f7f7;
  flex-wrap: nowrap;
  padding-top: 29px;
  padding-bottom: 29px;
  display: flex;
}

.footer__text {
  color: #545454;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
}

.footer__svg {
  width: 14px;
  height: 13px;
  fill: #ff6b08;
  margin-left: 10px;
  margin-right: 10px;
}

.footer__link {
  color: #545454;
  margin-left: 5px;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
  position: relative;
}

.footer__link:after {
  content: "";
  width: 100%;
  height: .5px;
  background-color: #545454;
  display: block;
  position: absolute;
  bottom: 3px;
  left: 0;
}

.footer__info {
  justify-content: center;
  align-items: baseline;
  display: flex;
}

